import React, { Component } from 'react';
import Select from 'react-select';
import InputMask from 'react-input-mask';
import style from './formForModal.module.css';
import './selectStyle.css';
import submit from './submitModalForm';
import textSuccess from '../../../img/elements/textSuccess.png';
import textError from '../../../img/elements/textError.png';
import textSuccessW from '../../../img/elements/textSuccess.webp';
import textErrorW from '../../../img/elements/textError.webp';

const oblastOptions = [
  { value: 'odessa', label: 'Одесская область' },
  { value: 'dnepropetrovsk', label: 'Днепропетровская область' },
  { value: 'chernigov', label: 'Черниговская область' },
  { value: 'kharkov', label: 'Харьковская область' },
  { value: 'jitomir', label: 'Житомирская область' },
  { value: 'poltava', label: 'Полтавская область' },
  { value: 'herson', label: 'Херсонская область' },
  { value: 'kievskaya', label: 'Киевская область' },
  { value: 'kiev', label: 'Киев' },
  { value: 'zaporoje', label: 'Запорожская область' },
  { value: 'vinnitsa', label: 'Винницкая область' },
  { value: 'kirovograd', label: 'Кировоградская область' },
  { value: 'nikolaev', label: 'Николаевская область' },
  { value: 'summi', label: 'Сумская область' },
  { value: 'lvov', label: 'Львовская область' },
  { value: 'cherkassi', label: 'Черкасская область' },
  { value: 'hmelnitsk', label: 'Хмельницкая область' },
  { value: 'volin', label: 'Волынская область' },
  { value: 'rovno', label: 'Ровенская область' },
  { value: 'ivano-frankovsk', label: 'Ивано-Франковская область' },
  { value: 'ternopol', label: 'Тернопольская область' },
  { value: 'zakorpatie', label: 'Закарпатская область' },
  { value: 'chernovetskaya', label: 'Черновецкая область' },
];

const bQuant = [
  { value: '1000', label: 'до 1 тыс.' },
  { value: '1000-5000', label: '1-5 тыс.' },
  { value: '5000-10000', label: ' 5-10 тыс.' },
  { value: '10000-50000', label: '10-50 тыс.' },
  { value: '50000+', label: ' более 50 тыс.' },
];

export default class FormForModal extends Component {
  state = {
    selectedOblast: '',
    companyName: '',
    billingQuantity: '',
    position: '',
    phone: '',
    email: '',
    responseErr: '',
    responseSuccess: '',
    responseErrFields: '',
  };

  handleChange = e => {
    const { target } = e;
    this.setState({ [target.name]: target.value });
  };

  onHandleChangeOblast = e => {
    this.setState({ selectedOblast: e });
  };

  onHandleChangeBill = e => this.setState({ billingQuantity: e });

  closeText = () =>
    this.setState({
      responseErr: '',
      responseSuccess: '',
      responseErrFields: '',
    });

  setStatusTextSuccess = () => {
    this.setState({
      responseSuccess:
        'Ваша заявка принята!\nС Вами свяжутся в ближайшее время',
    });
  };

  setStatusTextError = () => {
    this.setState({
      responseErr:
        'При отправке заявки возникла ошибка.\nПожалуйса, попробуйте еще раз',
    });
  };

  fetchData = (
    selectedOblast,
    companyName,
    billingQuantity,
    position,
    phone,
    email,
  ) => {
    submit(selectedOblast, companyName, billingQuantity, position, phone, email)
      .then(response => {
        if (response.data.code === 200) this.setStatusTextSuccess();
        else {
          this.setStatusTextError();
        }
      })
      .catch(error => {
        this.setStatusTextError();
      });
  };

  onHandleSubmit = e => {
    e.preventDefault();
    const {
      selectedOblast,
      companyName,
      billingQuantity,
      position,
      phone,
      email,
    } = this.state;
    if (selectedOblast !== '' && billingQuantity !== '') {
      this.fetchData(
        selectedOblast,
        companyName,
        billingQuantity,
        position,
        phone,
        email,
      );
      this.setState({
        selectedOblast: '',
        companyName: '',
        billingQuantity: '',
        position: '',
        phone: '',
        email: '',
      });
    } else {
      this.setState({
        responseErrFields: 'Пожалуйста, заполните все поля',
      });
    }
  };

  render() {
    const {
      selectedOblast,
      companyName,
      billingQuantity,
      position,
      phone,
      email,
      responseErr,
      responseSuccess,
      responseErrFields,
    } = this.state;
    return (
      <form
        onClick={this.closeText}
        onSubmit={this.onHandleSubmit}
        className={style.form}
      >
        <Select
          className={style.selectWrapper}
          classNamePrefix="selectWrapper"
          placeholder="Область"
          value={selectedOblast}
          onChange={this.onHandleChangeOblast}
          options={oblastOptions}
        />
        <input
          className={style.input}
          minLength={3}
          required
          placeholder="Наименование предприятия"
          onChange={this.handleChange}
          value={companyName}
          type="text"
          name="companyName"
        />
        <Select
          className={style.selectWrapper}
          classNamePrefix="selectWrapper"
          required
          placeholder="Количество лицевых счетов"
          value={billingQuantity}
          onChange={this.onHandleChangeBill}
          options={bQuant}
        />
        <input
          className={style.input}
          required
          minLength={3}
          placeholder="Как к Вам обращаться? / Должность"
          onChange={this.handleChange}
          value={position}
          type="text"
          name="position"
        />
        <InputMask
          className={style.input}
          required
          name="phone"
          onChange={this.handleChange}
          type="tel"
          placeholder="Телефон"
          value={phone}
          mask="+3 (8099) 999-99-99"
        />
        <input
          className={style.input}
          required
          placeholder="E-mail"
          onChange={this.handleChange}
          value={email}
          type="email"
          name="email"
        />
        <p>*Все поля обязательны для заполнения</p>
        <button className={style.submit} type="submit" value="узнать цену">
          оставить заявку
          {responseErr && (
            <span className={style.statusTextErr}>
              <picture>
                <source srcSet={textErrorW} type="image/webp" />
                <source srcSet={textError} type="image/png" />
                <img className={style.icon} src={textError} alt="icon" />
              </picture>
              {responseErr}
            </span>
          )}
          {responseErrFields && (
            <span className={style.statusTextErr}>
              <img className={style.icon} src={textError} alt="icon" />
              {responseErrFields}
            </span>
          )}
          {responseSuccess && (
            <span className={style.statusTextSuccess}>
              <picture>
                <source srcSet={textSuccessW} type="image/webp" />
                <source srcSet={textSuccess} type="image/png" />
                <img className={style.icon} src={textSuccess} alt="icon" />
              </picture>
              {responseSuccess}
            </span>
          )}
        </button>
      </form>
    );
  }
}
