import React, { Component } from 'react';
import Modal from 'react-responsive-modal';
import FormForModal from './formForModal/formForModal';
import style from './modalForm.module.css';
import logo from '../../img/logo/LogoCCT.png';
import logoW from '../../img/logo/LogoCCT.webp';

const obj = {
  modal: style.modal,
  overlay: style.overlay,
};

export default class ModalForm extends Component {
  state = {};

  render() {
    const { open, onCloseModal } = this.props;
    return (
      <div>
        <Modal
          classNames={obj}
          showCloseIcon={false}
          open={open}
          onClose={onCloseModal}
          center
        >
          <div className={style.wrapper}>
            <picture>
              <source srcSet={logoW} type="image/webp" />
              <source srcSet={logo} type="image/png" />
              <img className={style.logo} src={logo} alt="Logo" />
            </picture>
            <i onClick={onCloseModal} className="material-icons">
              close
            </i>
            <FormForModal />
          </div>
        </Modal>
      </div>
    );
  }
}
